@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700,900|Playfair+Display:400,700&display=swap);
@media (max-width: 991.98px) {
  .Navigation {
    display: none; } }

@media (min-width: 992px) {
  .Header .Navigation a, .Header .Navigation a:hover, .Header .Navigation a:visited, .Header .Navigation a:active, .Header .Navigation a:link {
    color: #F93030; }
  .Header .Navigation li:hover {
    border-bottom: 2px solid #FFFFFF; }
  .Intro .Navigation a, .Intro .Navigation a:hover, .Intro .Navigation a:visited, .Intro .Navigation a:active, .Intro .Navigation a:link {
    color: #000000; }
  .Intro .Navigation li:hover {
    border-bottom: 2px solid #FFFFFF; }
  .CV .Navigation a, .CV .Navigation a:hover, .CV .Navigation a:visited, .CV .Navigation a:active, .CV .Navigation a:link {
    color: #F93030; }
  .CV .Navigation li:hover {
    border-bottom: 2px solid #FFFFFF; }
  .Projects .Navigation a, .Projects .Navigation a:hover, .Projects .Navigation a:visited, .Projects .Navigation a:active, .Projects .Navigation a:link {
    color: #000000; }
  .Projects .Navigation li:hover {
    border-bottom: 2px solid #F93030; }
  .Navigation {
    display: inline;
    margin: 0;
    padding: 0 0 30px 0; }
  .Navigation > li {
    display: inline;
    margin: 0 10px; }
    .Navigation > li:hover {
      padding-bottom: 10px;
      cursor: pointer; }
  .Navigation > li > a {
    font-weight: 800;
    font-size: 18px;
    padding-bottom: 10px; } }

.Header {
  background-color: #292929;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .Header .Navigation {
    color: #F93030; }

.Caption {
  padding-top: 40px;
  text-transform: uppercase;
  color: #F93030;
  font-weight: 500; }

.Logo {
  height: 200px; }

.Footer {
  color: white;
  text-align: center;
  height: 100vh; }
  .Footer h1 {
    padding: 50px 0; }
  .Footer svg {
    fill: white;
    width: 40px;
    height: 40px;
    margin: 0 10px; }
  .Footer svg:hover {
    fill: #F93030; }
  .Footer .Copyrights {
    position: absolute;
    bottom: 20px;
    width: 100%;
    color: #F93030;
    font-weight: 100;
    font-size: 16px; }

.Main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  height: 100vh; }
  .Main h2 {
    padding-top: 25vh; }

.Content {
  background-color: #FFFFFF;
  min-height: 100vh;
  line-height: 25px;
  padding: 5vh 5vw; }

.FullWidth {
  min-height: 100vh;
  background-color: #292929; }

.Spacer {
  margin-bottom: 50px; }

@media (min-width: 992px) {
  .Flexbox {
    display: flex; }
    .Flexbox > div {
      width: 50vw;
      flex-shrink: 0; }
  .Main {
    width: 50vw; } }

.Bubble {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 80vh;
  padding: 10px;
  width: 100px;
  height: 100px;
  -webkit-transition: opacity 533.33333ms;
  transition: opacity 533.33333ms;
  opacity: 0;
  background-color: #F93030;
  border-radius: 50%; }
  .Bubble:hover {
    cursor: pointer; }

.ProjectHead:hover > .Bubble {
  opacity: 1;
  -webkit-transition: opacity 800ms;
  transition: opacity 800ms; }

.Project {
  width: 50vw;
  height: 100vh; }
  .Project .ProjectHead {
    width: 50vw;
    height: 100vh;
    background-image: url("http://place-hoff.com/2000/2000");
    display: flex;
    align-items: center;
    flex-direction: column;
    -webkit-transition: opacity 800ms;
    transition: opacity 800ms;
    text-align: center;
    position: relative; }
    .Project .ProjectHead:hover {
      opacity: 0.7; }
  .Project .ProjectTitle {
    background-color: #000000;
    color: white;
    padding: 20px;
    position: absolute;
    top: 10%;
    font-family: 'Playfair Display', serif;
    font-size: 18px;
    border-radius: 3px; }

.Hidden {
  display: none;
  -webkit-transition: display 500ms;
  transition: display 500ms; }

.Overlay {
  min-height: 100vh;
  background-color: #FFFFFF;
  position: relative; }
  .Overlay > svg {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 25px;
    top: 10px;
    fill: #000000; }
  .Overlay > svg:hover {
    fill: #F93030;
    cursor: pointer; }

html {
  scroll-behavior: smooth;
  font-family: 'Lato', sans-serif;
  font-size: 17px; }

body {
  margin: 0; }

h1 {
  font-family: 'Playfair Display', serif;
  font-weight: 500;
  font-size: 42px;
  margin: 0 30px; }

h2 {
  font-weight: 600;
  font-size: 36px;
  margin: 0 30px; }

h3 {
  font-weight: 600;
  font-size: 18px; }

strong {
  color: #F93030;
  font-weight: normal; }

a, a:hover, a:visited, a:active, a:link {
  text-decoration: none;
  text-transform: lowercase; }

.Wrapper {
  position: relative;
  top: 0;
  width: 100%;
  overflow: hidden; }

.Intro {
  background-color: #F93030; }

.CV {
  background-color: #292929; }

.Contact {
  background-color: #292929; }

*, *:before, *:after {
  box-sizing: border-box; }

