$primary: #F93030; //#ff4468
$secondary: #292929;
$black: #000000;
$white: #FFFFFF;

$transactionSpeed: 800ms;

@mixin NavColors($link, $border) {
    a, a:hover, a:visited, a:active, a:link {
        color: $link;
    }

    li:hover {
        border-bottom: 2px solid $border;
    }
}